input[type="checkbox"] {
  width: 90px;
  height: 30px;
  -webkit-appearance: none;
  cursor: pointer;
  background: linear-gradient(0deg, #333, #000);
  border-radius: 20px;
  outline: none;
  position: relative;
  box-shadow: 0 0 0 4px #353535, 0 0 0 5px #3e3e3e, inset 0 0 10px #000, 0 5px 20px rgba(0, 0, 0, .5), inset 0 0 15px rgba(0, 0, 0, .2);
}

input:checked[type="checkbox"] {
  background: linear-gradient(0deg, #6dd1ff, #20b7ff);
  box-shadow: 0 0 2px #6dd1ff, 0 0 0 4px #353535, 0 0 0 5px #3e3e3e, inset 0 0 10px #000, 0 5px 20px rgba(0, 0, 0, .5), inset 0 0 15px rgba(0, 0, 0, .2);
}

input[type="checkbox"]:before {
  content: "";
  width: 60px;
  height: 30px;
  z-index: 2;
  background: linear-gradient(0deg, #000, #6b6b6b);
  border-radius: 20px;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(.98, .96);
  box-shadow: 0 0 0 1px #232323;
}

input:checked[type="checkbox"]:before {
  left: 30px;
}

input[type="checkbox"]:after {
  content: "";
  width: 3px;
  height: 3px;
  z-index: 3;
  background: linear-gradient(0deg, #6b6b6b, #000);
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: calc(50% - 2px);
  left: 50px;
}

input:checked[type="checkbox"]:after {
  background: #63cdff;
  left: 80px;
  box-shadow: 0 0 5px #13b3ff, 0 0 15px #13b3ff;
}

